	module.exports = {"data":{"post":{"id":"/Users/prasanna/projects/blog/content/posts/2011-04-24--wiring-android-wheel-widget-into-your-application/index.md absPath of file >>> MarkdownRemark","html":"<p>I just saw this widget (<a href=\"http://code.google.com/p/android-wheel/\">Android Wheel</a>) and instantly got attracted to it. Just wanted to get this into my app. So here are the steps i followed to integrate this into my application.</p>\n<ul>\n<li>Import the project into Eclipse through Svn import (File->Import) from google-code (<a href=\"http://android-wheel.googlecode.com/svn/trunk\">http://android-wheel.googlecode.com/svn/trunk</a>)</li>\n<li>Once you import the project in the Eclipse, Right click on the project -> Properties -> Choose android tab and check the IsLibrary option</li>\n<li>Now Right click on your project -> Properties -> Choose android tab -> In the Library section add the imported project.</li>\n</ul>\n<p>That’s it you are good to go now. The Apps in the Wheel Demo are really good enough to guide you through.</p>","htmlAst":{"type":"root","children":[{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"I just saw this widget ("},{"type":"element","tagName":"a","properties":{"href":"http://code.google.com/p/android-wheel/"},"children":[{"type":"text","value":"Android Wheel"}]},{"type":"text","value":") and instantly got attracted to it. Just wanted to get this into my app. So here are the steps i followed to integrate this into my application."}]},{"type":"text","value":"\n"},{"type":"element","tagName":"ul","properties":{},"children":[{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"Import the project into Eclipse through Svn import (File->Import) from google-code ("},{"type":"element","tagName":"a","properties":{"href":"http://android-wheel.googlecode.com/svn/trunk"},"children":[{"type":"text","value":"http://android-wheel.googlecode.com/svn/trunk"}]},{"type":"text","value":")"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"Once you import the project in the Eclipse, Right click on the project -> Properties -> Choose android tab and check the IsLibrary option"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"Now Right click on your project -> Properties -> Choose android tab -> In the Library section add the imported project."}]},{"type":"text","value":"\n"}]},{"type":"text","value":"\n"},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"That’s it you are good to go now. The Apps in the Wheel Demo are really good enough to guide you through."}]}],"data":{"quirksMode":false}},"fields":{"slug":"/wiring-android-wheel-widget-into-your-application/","prefix":"2011-04-24"},"frontmatter":{"title":"Wiring Android Wheel Widget into your application.","subTitle":"Wheel component in iOS is quite famous. Integrating a similar android-wheel to your android application.","cover":null,"draft":null}},"author":{"id":"/Users/prasanna/projects/blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Prasanna</strong> is a full stack web developer, with loads of experience in architecting and coding enterprise software solutions.  </p>"},"footnote":{"id":"/Users/prasanna/projects/blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>rants and writings through personal experience</li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":"670156599751120%"}}}},"pathContext":{"slug":"/wiring-android-wheel-widget-into-your-application/"}}

